
import { GoogleSpreadsheet } from 'google-spreadsheet';
// import  creds from './credentials.json';



async function appendSpreadsheet(newRow) {
    const creds = 
    {
        "type": "service_account",
        "project_id": process.env.REACT_APP_PROJECT_ID,
        "private_key_id": process.env.REACT_APP_PRIVATE_KEY_ID,
        "private_key": process.env.REACT_APP_PRIVATE_KEY?.replace(/\\n/g, '\n'),
        "client_email": process.env.REACT_APP_CLIENT_EMAIL,
        "client_id": process.env.REACT_APP_CLIENT_ID,
        "auth_uri": "https://accounts.google.com/o/oauth2/auth",
        "token_uri": "https://oauth2.googleapis.com/token",
        "auth_provider_x509_cert_url": "https://www.googleapis.com/oauth2/v1/certs",
        "client_x509_cert_url": "https://www.googleapis.com/robot/v1/metadata/x509/googlesheet%40arctic-signer-341307.iam.gserviceaccount.com"
      }
    console.log(process.env)
  const doc = new GoogleSpreadsheet('1EnXsMernyQFk1DmMgGT_rSXFwTOUtjzKWNltXD3zic0');
  await doc.useServiceAccountAuth(creds);
  await doc.loadInfo();
  const sheet = doc.sheetsById[0];

  const result = await sheet.addRow(newRow);
  return result;
};

export default appendSpreadsheet